/* ViewComplaintStatus.css */

.complaint-status-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  color: black; /* Default text color inside the container */
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  color: black; /* Ensures the header text is black */
}

.complaints-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.complaint-box {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: white; /* Ensures the complaint box background is white */
}

.complaint-box p, /* Targets all paragraph text */
.complaint-box strong, /* Targets all strong text */
.complaint-box span { /* Targets all span text, including status labels */
  color: black; /* Ensures all text within the complaint box is black */
}

.status {
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 4px;
  display: inline-block;
  color: #fff; /* Text color for status labels */
}

.status.pending {
  background-color: #f39c12; /* Orange background for pending status */
}

.status.resolved {
  background-color: #2ecc71; /* Green background for resolved status */
}

.status.rejected {
  background-color: #e74c3c; /* Red background for rejected status */
}
