/* Center the role selection box */
.role-selection-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(135deg, #a8c0ff, #3f2b96);
}

.header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.app-logo {
    width: 50px; /* Adjust size as needed */
    height: 50px;
    margin-right: 15px;
}

.app-name {
    font-size: 30px;
    color: white;
    font-weight: bold;
}

.role-selection-box {
    background-color: white;
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 300px;
}

h1 {
    margin-bottom: 20px;
    font-weight: bold;
}

button {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 25px;
    background-color: #a28ff1;
    color: white;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-bottom: 10px;
}

button:hover {
    background-color: #8c7ae6;
}

.header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  
 